import { AppBar, Typography, Container, Toolbar, IconButton, Drawer, List, ListItemButton, ListItemText, ListItemIcon, TextField, Button, FormControl, FormControlLabel, FormHelperText, FormLabel, Radio, RadioGroup} from '@mui/material';
import { useState, useRef } from 'react';

import { Link } from 'react-router-dom';

import HomeIcon from '@mui/icons-material/Home';
import MenuIcon from '@mui/icons-material/Menu';
import AnalyticsIcon from '@mui/icons-material/Analytics';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import CreateIcon from '@mui/icons-material/Create';

interface Tiedot {
    [key : string] : any;
    joukkueenNimi? : string;
    kapteeninNimi? : string;
    peliMuoto? : string;
}

interface Virheviestit extends Tiedot {}


const Ilmoita : React.FC = () : React.ReactElement => {

    const [virheviestit, setVirheviestit] = useState<Virheviestit>({});

    const tiedot : Tiedot = useRef<Tiedot>({});


    const formHandler = (e : React.FormEvent) : void => {

        e.preventDefault();

        let virhelista : Virheviestit = {};

        if(!tiedot.current.joukkueenNimi) {
            tiedot.current.joukkueenNimi = "";
        }
        if(!tiedot.current.kapteeninNimi) {
            tiedot.current.kapteeninNimi = "";
        }

        if(tiedot.current.joukkueenNimi.length < 4) {
            virhelista = {...virhelista, joukkueenNimi : "Anna joukkueen nimi, väh. 4 merkkiä"};
        }

        if(tiedot.current.kapteeninNimi.length < 2) {
            virhelista = {...virhelista, kapteeninNimi : "Anna joukkueen kapteenin nimi, väh. 2 merkkiä"};
        }

        if(!tiedot.current.peliMuoto) {
            virhelista = {...virhelista, peliMuoto : "Ilmoita pelimuoto"};
        }


        

        if(Object.entries(virhelista).length > 0) {
            setVirheviestit({...virhelista});
        } else {
            setVirheviestit({});
            window.alert("Ilmoittautuminen onnistui, kiitos!");
        }


    }

    const inputHandler = (e : React.ChangeEvent<HTMLInputElement>) : void => {

        tiedot.current[e.target.name] = e.target.value;

    }


    const [valikkokytkin, setValikkokytkin] = useState<boolean>(false);

    return (
        <>
            <Container style={{"border": "1px solid black"}} maxWidth="md">

            <AppBar position="static">
                    <Toolbar>
                        <IconButton onClick={() => setValikkokytkin(true)}>
                            <MenuIcon/>
                        </IconButton>

                        
                        <Drawer open={valikkokytkin} onClose={() => setValikkokytkin(false)}>

                            <List onClick={() => setValikkokytkin(false)}>

                                <ListItemButton component={Link} to="/">
                                    <ListItemIcon>
                                        <HomeIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Etusivu" />
                                </ListItemButton>

                                <ListItemButton component={Link} to="/ilmoita">
                                    <ListItemIcon>
                                        <AccountBoxIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Ilmoita joukkueesi" />
                                </ListItemButton>

                                <ListItemButton component={Link} to="/vieraskirja">
                                    <ListItemIcon>
                                        <CreateIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Vieraskirja" />
                                </ListItemButton>


                            </List>

                        </Drawer>

                        <Typography component="div" sx={{fontSize: "25pt", flexGrow: 1}}>Superliiga - Liigailmoittautuminen</Typography>


                    </Toolbar>

                </AppBar>

                <h2>Ilmoita joukkueesi liigaan:</h2>
                <br></br>

                <form onSubmit={formHandler}>
                        <p>Joukkueen nimi:</p>
                        <TextField
                            name="joukkueenNimi"
                            onChange={inputHandler}
                            error={Boolean(virheviestit.joukkueenNimi)}
                            helperText={virheviestit.joukkueenNimi}
                            label="Joukkueen nimi"
                            variant="outlined"
                        /><br></br><br></br>

                        <p>Kapteenin nimi:</p>
                        <TextField
                            name="kapteeninNimi"
                            onChange={inputHandler}
                            error={Boolean(virheviestit.kapteeninNimi)}
                            helperText={virheviestit.kapteeninNimi}
                            label="Kapteenin nimi"
                            variant="outlined"
                        /><br></br><br></br>


                        <FormControl onChange={inputHandler}>

                            <FormLabel>Pelimuoto:</FormLabel>
                            <FormHelperText error={Boolean(virheviestit.peliMuoto)}>
                                {virheviestit.peliMuoto}
                            </FormHelperText>
                            <RadioGroup name="peliMuoto">
                                <FormControlLabel value="3v3" control={<Radio />} label="3v3" />
                                <FormControlLabel value="4v4" control={<Radio />} label="4v4" />
                                <FormControlLabel value="5v5" control={<Radio />} label="5v5" />
                            </RadioGroup>
                            
                        </FormControl><br></br>

                        <Button
                        type="submit"
                        variant='contained'
                        >Ilmoita joukkue</Button>
                </form>
            </Container>
        </>
    );
}

export default Ilmoita;