import React from 'react';
import { useState } from 'react';
import { AppBar, Typography, Container, Toolbar, IconButton, Drawer, List, ListItemButton, ListItemText, ListItemIcon, TextField, Button, FormControl, FormControlLabel, FormHelperText, FormLabel, Radio, RadioGroup} from '@mui/material';


import { Link } from 'react-router-dom';

import HomeIcon from '@mui/icons-material/Home';
import MenuIcon from '@mui/icons-material/Menu';
import AnalyticsIcon from '@mui/icons-material/Analytics';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import CreateIcon from '@mui/icons-material/Create';

const Vieraskirja : React.FC = () : React.ReactElement => {

    const [kaikkiViestit, setKaikkiViestit] = useState<string[]>(["hyvää peliä", "kova kiri t. Jeppe", "halli täynnä taas"]);

    const [uusiViesti, setUusiViesti]= useState<string>("");

  const lisaaViesti = () : void => {

    if(uusiViesti.length > 0) {
        setKaikkiViestit([...kaikkiViestit, uusiViesti]);
    }

  }


  const [valikkokytkin, setValikkokytkin] = useState<boolean>(false);

  return (
    <>
        <Container style={{"border": "1px solid black"}} maxWidth="md">

        <AppBar position="static">
                    <Toolbar>
                        <IconButton onClick={() => setValikkokytkin(true)}>
                            <MenuIcon/>
                        </IconButton>

                        
                        <Drawer open={valikkokytkin} onClose={() => setValikkokytkin(false)}>

                            <List onClick={() => setValikkokytkin(false)}>

                                <ListItemButton component={Link} to="/">
                                    <ListItemIcon>
                                        <HomeIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Etusivu" />
                                </ListItemButton>

                                <ListItemButton component={Link} to="/ilmoita">
                                    <ListItemIcon>
                                        <AccountBoxIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Ilmoita joukkueesi" />
                                </ListItemButton>

                                <ListItemButton component={Link} to="/vieraskirja">
                                    <ListItemIcon>
                                        <CreateIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Vieraskirja" />
                                </ListItemButton>


                            </List>

                        </Drawer>

                        <Typography component="div" sx={{fontSize: "25pt", flexGrow: 1}}>Superliiga - Liigailmoittautuminen</Typography>


                    </Toolbar>

                </AppBar>

            <h2>Vieraskirja</h2>
            <p><b>Hallissa huikitaan! Lisää oma viestisi!</b></p>
            <br></br><br></br>

            <table>

                {kaikkiViestit.map((viesti : string) => {

                return (
                    <>
                        <tr>
                        <td>- {viesti}</td>
                        </tr>
                        <br></br>
                    </>
                );
        
                })}
                
            </table>

            
            <br></br>
            <TextField label="viestisi" variant="outlined" onKeyDown = {(e) => {setUusiViesti((e.target as HTMLInputElement).value)}} />
            <Button variant="contained" onClick = {lisaaViesti}>Lisää viesti</Button>

        </Container>

    </>
  );


}


export default Vieraskirja;