import { AppBar, Typography, Container, Toolbar, IconButton, Drawer, List, ListItemButton, ListItemText, ListItemIcon} from '@mui/material';

import { useState, useRef } from 'react';
import { Link } from 'react-router-dom';

import HomeIcon from '@mui/icons-material/Home';
import MenuIcon from '@mui/icons-material/Menu';
import AnalyticsIcon from '@mui/icons-material/Analytics';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import CreateIcon from '@mui/icons-material/Create';



const Tilastot : React.FC = () : React.ReactElement => {

    const [valikkokytkin, setValikkokytkin] = useState<boolean>(false);

    return (
        <>
            <Container style={{"border": "1px solid black"}} maxWidth="md">

            <AppBar position="static">
                    <Toolbar>
                        <IconButton onClick={() => setValikkokytkin(true)}>
                            <MenuIcon/>
                        </IconButton>

                        
                        <Drawer open={valikkokytkin} onClose={() => setValikkokytkin(false)}>

                            <List onClick={() => setValikkokytkin(false)}>

                                <ListItemButton component={Link} to="/">
                                    <ListItemIcon>
                                        <HomeIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Etusivu" />
                                </ListItemButton>

                                <ListItemButton component={Link} to="/tilastot">
                                    <ListItemIcon>
                                        <AnalyticsIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Tilastot" />
                                </ListItemButton>

                                <ListItemButton component={Link} to="/ilmoita">
                                    <ListItemIcon>
                                        <AccountBoxIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Ilmoita joukkueesi" />
                                </ListItemButton>

                                <ListItemButton component={Link} to="/vieraskirja">
                                    <ListItemIcon>
                                        <CreateIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary="Vieraskirja" />
                                </ListItemButton>


                            </List>

                        </Drawer>

                        <Typography component="div" sx={{fontSize: "25pt", flexGrow: 1}}>Superliiga - Liigailmoittautuminen</Typography>


                    </Toolbar>

                </AppBar>

                <p>Tere</p>
                
            </Container>
        </>
    );
}

export default Tilastot;